<template>
  <div id="app">
    <vue-p5
      @setup="setup"
      @draw="draw"
    />
  </div>
</template>

<script>
import VueP5 from 'vue-p5'

export default {
  name: 'app',

  components: {
    VueP5
  },

  data () {
    return {
      grid: {
        cols: 0,
        rows: 0,
        width: 0,
        height: 0,
        dots: []
      },
      dot: {
        maxWidth: 30,
        maxHeight: 30,
        minWidth: 0,
        minHeight: 0
      },
      sk: null,
      timer: null
    }
  },

  methods: {
    setup (sk) {
      this.sk = sk
      this.setupGrid(sk.windowWidth, sk.windowHeight)

      window.addEventListener('resize', this.debounceResize)

      sk.createCanvas(sk.windowWidth, sk.windowHeight)
      sk.background(25, 25, 25, 255)
      sk.noStroke()
      sk.fill(127, 127, 127, 255)
    },

    resize () {
      this.setupGrid(this.sk.windowWidth, this.sk.windowHeight)
      this.sk.resizeCanvas(this.sk.windowWidth, this.sk.windowHeight)
    },

    debounceResize () {
      clearTimeout(this.timer)
      this.timer = window.setTimeout(this.resize, 100)
    },

    draw (sk) {
      // draw a line between the previous
      // and the current mouse position
      // sk.line(sk.pmouseX, sk.pmouseY, sk.mouseX, sk.mouseY)
      sk.background(25, 25, 25, 255)

      this.fill()
    },

    setupGrid (width, height) {
      this.grid.dots = []
      this.grid.width = width
      this.grid.height = height

      this.grid.cols = Math.floor(width / this.dot.maxWidth)
      this.grid.rows = Math.floor(height / this.dot.maxHeight)

      for (let i = 0; i < this.grid.cols; i++) {
        for (let j = 0; j < this.grid.cols; j++) {
          // const size = Math.floor(Math.random() * 11)
          const size = Math.floor(Math.random() * 10)
          const hasSize = !!size

          this.grid.dots.push({
            x: i * this.dot.maxWidth,
            y: j * this.dot.maxHeight,
            w: size,
            h: size,
            grow: size > 0 && size < 10 ? Math.random() < 0.5 : !hasSize
          })
        }
      }
    },

    fill () {
      this.sk.ellipseMode(this.sk.RADIUS)
      this.grid.dots.forEach((dot) => {
        const hasSize = !!dot.w
        dot.grow = dot.w > 0 && dot.w < 10 ? dot.grow : !hasSize

        const mod = dot.grow ? 1 : -1
        dot.w = dot.w + mod
        dot.h = dot.h + mod
        this.sk.ellipse(dot.x, dot.y, dot.w, dot.h)
      })
    }
  }
}
</script>

<style>
canvas {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

#app {
  position: relative;
  z-index: 2;
}
</style>
